// estas son las portadas finales 
let portadasRegistradas = [];

function CardPortada({ img, id }) {
   
    const eliminarPortada = (e) => {
        // ahora preguntamos si estamo seguros que queremos eliminar
        if (window.confirm('Esta seguro que desea eliminar esta portada?')) {
            fetch('https://web-production-eeb4.up.railway.app/portada/' + id, {
                method: 'DELETE',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },

            }).then((Response) => Response.json()).then((d) => {

                console.log(d)
                window.location.reload(true)
            })
        }


    }

    return (
        <div className='cardPortada'>

            <button className='btneliminar btn btn-danger'><i class="bi bi-trash3-fill" onClick={eliminarPortada}>Eliminar</i></button>
            <img src={img} alt="yo soy la imagen" />

        </div>
    )
}

function Portada() {
    // llamamos la api de portada para el get
    //const navegacion = useNavigate();
   /*  const username = localStorage.getItem('nombre')
   
    if(username==="")
    {
        console.log(username)  //navegacion('/cm-diseno/IniciarSesion')
    } */


    const [portadas, setPortadas] = useState([])
    const [pruebaPortadas, setPruebaPortadas] = useState([])


    let testPaginas = [];
    const mostrarPortadas = () => {

        for (let index = 0; index < portadas.length; index++) {
            testPaginas.push(portadas[index])
            portadasRegistradas.push(portadas[index])
        }
        setPruebaPortadas(testPaginas)
        getPortadas();

    }

    const ocultar = () => {

        setPruebaPortadas([])
    }



    // ste mi codigo que me trae todas las portadas

    const getPortadas = async () => {
        const peticion = await fetch('https://web-production-eeb4.up.railway.app/portadas').then(Response => Response.json())
        const respuesta = await peticion;
        setPortadas(respuesta)
    }

    window.addEventListener('load', () => {
        getPortadas();
    })
   

    useEffect(()=>
    {
        getPortadas();

    },[portadas])

    window.addEventListener('load', ()=>
    {
        getPortadas();
    })



    // ahora llenamos las portadas registradas para tener manejo de la data fuera del servidor 

    /*     portadas.forEach(e=>
            {
                portadasRegistradas.push(e)
            })
 */






    const newImg = async (e) => {
        e.preventDefault()
        /*  const file = e.target.textFile.value; */
        const peticion = await fetch('https://web-production-eeb4.up.railway.app/portada/subir',
            {
                method: 'POST',
                body: new FormData(e.currentTarget)

            }
        ).then(Response => Response.json())
        const respuesta = await peticion;

        e.target.textFile.value = '';

        console.log(pruebaPortadas)
        getPortadas();

    }





    return (
        <div className='container' id='contenedorPortadas'>
          
            <div className="row fila">

                <div className='text-center' id='formularioPortadas'>
                    <h5>Formulario de registro de portadas</h5>
                    <div>
                        <form onSubmit={newImg}>
                            <img src={imgData} style={{ width: '250px' }} alt="" />
                            <div><input type='file' id='textFile' name='imagen' required='true' className='form-control mb-2'></input></div>
                            <input type='submit' value='GUARDAR' className=' form-control  btnGuardar mb-3'></input>
                        </form>
                        <h4>Portadas registradas:   {portadas.length}</h4>


                    </div>

                </div>

                <div className='botones'>
                <input type="button" className='form-control btn btn-success  mb-3' value="Mostrar portadas registradas" onClick={mostrarPortadas} />
                    <input type="button" className='form-control btn btn-primary' value="Ocultar portadas" onClick={ocultar} />

                </div>


                <div className='contenedorPortadas'>
                    {
                        pruebaPortadas.map((element) =>
                        (
                            <CardPortada key={element._id} img={element.url} id={element._id} />
                        ))

                    }
              
                 
                </div>
              


                {/* wsegunda columna del segundo formulario */}









            </div>

        </div>
    )
}

