import '../Estilos/Publicidad.css'
import Aos from 'aos'
Aos.init()

function Publicidad() {
    return (
        <div className="container">
            <PostOne />
            <PostTwo />


        </div>
    )
}


function PostOne() {
    return (
        <div className="row panelPublicidad">
            <div className="col12 col-sm-12 col-md-6 col-lg-6">
                <h3 className='publicidadTitulo'>¿Sabias por que es importante la publicidad?</h3>
                <img src={require('../img/Interrogante.png')} alt="Imagen de Interrogante" id="interrogante" />
            </div>

            <div className="col col-sm-12 col-md-6 col-lg-6 parrafoPublicidad">
                <p> <strong>La publicidad</strong> es la herramienta tradicional directa del marketing, y tiene los objetivos de divulgar un producto para estimular su consumo, transmitir un mensaje positivo con relación a una marca y fortalecer la presencia de una empresa en el mercado. <br /> <br />
                    <strong>¿Qué es la publicidad? <br /><b></b></strong>

                    La publicidad forma parte de nuestras vidas. Está en las calles, en los centros comerciales, dentro de nuestras casas, en el periódico, en las revistas, en el celular, en la Internet. Hacia donde mires, está allí.

                    <br />
                    <br /> <b>"Debido a esto y a muchas otras cosas mas que no sabias estamos aqui para llevar tu marca o tu negocio al siguiente nivel. " </b></p>
            </div>

        </div>
    )
}


function PostTwo() {
    return (
        <div className="row panelPublicidad">

            <div className="col col-sm-12 col-md-5 col-lg-5 parrafoPublicidad">

                <h3 className='publicidadTitulo'>Tienes una idea de lo que quieres, cuentanos...</h3>
                <p>Nuestro trabajo es tomar esa idea que tengas y materializarla con todas las herramientas necesearias para que el resultado final llegue o tome al la direccion correcta. <br />
                    <br />
                    Hacemos que tus ideas tomen vida y cumplan los objetivos que quieres de una manera mas eficiente.
                </p>

            </div>

            <div className="col col-sm-12 col-md-7 col-lg-7">

                <img src={require('../img/Idea.png')} alt="Imagen de Interrogante" id="imgIdea" />
            </div>

        </div>
    )
}
export default Publicidad