import '../Estilos/BarraNavegacion.css'
import BtnMenu from './BtnMenu'
import { useState } from 'react'
import Publicidad from './Publicidad'
import { Link } from 'react-router-dom'

function BarraNavegacion() {
   
  
    const [accion, setAccion]= useState('ocultarlista')
    const navegacion =()=>
    {
        if(accion==='ocultarlista')
        {

            setAccion('listabotones')  
        }
        else
        {
            setAccion('ocultarlista')
           
        }
      
    }
   
   
    return (
        <div className="BarraNavegacion" id='inicio'>
            <div className='container contenedor-nav'>
                <div className='contenedorLogo'>
                <img src={require('../img/chatlogo.png')} alt="Logo" className='logo' />

                </div>
             
                <h3>𝑫𝒊𝒔𝒆ñ𝒐 𝑮𝒓𝒂𝒇𝒊𝒄𝒐</h3>
              

                <div className='navegacion'>
                  
                
                    <BtnMenu accion={navegacion}/>
                    

                    <ul className={accion}>
                        
                       <li><a className='btnMenu' href="#Inicio"> Inicio</a></li>
                        <li><a className='btnMenu' href="#Nosotros">Sobre Nosotros</a></li>
                        <li><a  className='btnMenu' href="#servicios">Servicios</a></li>
                        <li><a className='btnMenu' href="#portafolio">Portafolio</a></li>
                        <li><a  className='btnMenu' href="#Contactos">Contactos</a></li>
                     {/*    <Link to="/servicios" >Test</Link> */}
                     
                  
                    </ul>

                </div>


            </div>
        </div>
    )
}

export default BarraNavegacion