import { useEffect } from 'react';
import { useState } from 'react'
import('../Estilos/Trabajos2.css')


function Trabajos2({imagen})
{

    const [data, setData] = useState([])

    async function getData()
    {
        try {
            const ruta = 'https://web-production-eeb4.up.railway.app/trabajos';
            const peticion = await fetch(ruta).then(respuesta=>respuesta.json());
            const respuesta =  await peticion;
            setData(respuesta)
           
        } catch (error) {
            console.log(error)
        }
    }

    useEffect(()=>
    {   
        getData();

    }, [data])



    return(
        <section>
           
            {
                data.map((item)=>
                (
                   
                          <Imagen key={item._id} ruta={item.imagen} titulo={item.titulo} />
                          
                ))
            }

        </section>
    )
}


function Imagen({ruta, titulo})
{

    console.log(ruta)
    return(
      
     <img src={ruta} alt={titulo} />

    )
}


export default Trabajos2