
import PanelServicios from './PanelServicios'
import Navegacion from './NavegacionAdmin'
import '../Estilos/Servicios.css'
import { useState } from 'react'
import { useEffect } from 'react'




function Trabajos() {

    //esta funcion se encargara de acer a peticion get a nuestro servidor
    const [trabajos, setTrabajos] = useState([])
    const [edicion, setEdicion] = useState({}) 
    

    //nuevos valores de la edicion
 
  // esta es la gfuncion del getAll
    const getTrabajos = async () => {
        const peticion = await fetch('https://web-production-eeb4.up.railway.app/trabajos').then(Response => Response.json())
        const respuesta = await peticion;
        setTrabajos(respuesta);
       

        
    }

    useEffect(() => {
        getTrabajos();
    }, [trabajos])

      

    //addservicio
    const newTrabajo = async(e)=>
    {
        e.preventDefault(); 
       
        //si hay un id disponible sera una actualizacion
       /*  if(e.target.id.value!="")
        {
                console.log('testiando desde el put')
            const peticion = await fetch('http://localhost:3001/servicioupdate/' + e.target.id.value,
            {
                method :'POST',
                headers:
                {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },
                body : new FormData(e.currentTarget)
            }).then(Response=>Response.json())  
    
        } */


    
        const peticion = await fetch('htthttps://web-production-eeb4.up.railway.app/trabajo/subir',
        {
            method :'POST',
            body : new FormData(e.currentTarget)
        }).then(Response=>Response.json())
       
       

        e.target.id.value = '';
        e.target.titulo.value='';
        e.target.imagen.value = '';
        e.target.descripcion.value='';



        const respuesta = await peticion
       getTrabajos() 
 
        
    }


    const eliminar = async (id) => {
        console.log(id)
        if (window.confirm('Esta seguro que desea elminar este registro')) {
            const peticion = await fetch('hhttps://web-production-eeb4.up.railway.app/trabajo/' + id,
                {
                    method: 'DELETE',
                    headers:
                    {
                        'Accept': 'application/json',
                        'Content-Type': 'application/json'
                    }
                })

            const respuesta = await peticion
            console.log(respuesta)
        }

    }
     
    
    const editar=(e)=>
    {
          
         setEdicion(e)
         console.log(e)
          
    }

    function newTitulo (e)
    {
        setEdicion(()=>
        ({
           
            titulo: e.target.value

        }))
    }

    
    function newDescripcion (e)
    {
        setEdicion(()=>
        ({
           
            descripcion: e.target.value

        }))
    }
/* 
    function newImagen (e)
    {
        setEdicion(()=>
        ({
           
            imgurl: e.target.value

        }))
    }
 */
 

     
            const user = localStorage.getItem('username')
            if(user === " " || user===null)
            {
                return (
                    <div className='container'>
                        <h1>Para acceder a este panel debe iniciar sesión .</h1>
                        
                        <hr />
               
                    </div>
                )
            }
            else

            {

                return (

        
                    <div className="container">
                        <Navegacion />
            
                        <div className="contformulario">
                            <div className='ilustracion'>
                            <h4>Trabajos realizados</h4>
                            <img src={require ('../img/ilustraciontrabajo.jpg')  } alt="Trabajo realizado" />
                            </div>
            
            
            
                            <form onSubmit={newTrabajo}>
            
                            <div>
                                  
                                    <input type="text"  name="id" visible={false} value={edicion._id} spellcheck="true" id="id" className='form-control textid' />
                             </div>
            
                                <div>
                                    <label htmlFor="titulo">Titulo</label>
                                    <input type="text" name="titulo" spellcheck="true" value={edicion.titulo} onChange={newTitulo}  id="titulo" className='form-control' required='true' />
                                </div>
            
                                <div>
                                    <label htmlFor="descripcion">Descripción</label>
                                    <br />  
                                    <textarea name="descripcion" id="descripcion" spellcheck="true" value={edicion.descripcion} onChange={newDescripcion}  autocorrect="on" cols="30" rows="10" className='form-control'></textarea>
                                </div>
            
                                <div>
                                    <label htmlFor="imagen">Imagen</label>
                                    <input type="file" name="imagen" id="imagen" className='form-control'  required='true'  />
                                </div>
            
                                <div>
                                    <input type="submit" className='btn btn-primary mt-3 form-control' value='GUARDAR' />
                                </div>
                            </form>
            
            
                            <hr></hr>
            
                        </div>
            
                        <hr />
                        <h4>Trabajos Registrados</h4>
            
                        <hr />
            
                        <table class="table">
                            <thead class="thead-dark">
                                <tr>
            
                                    <th scope="col">Titulo </th>
                                    <th scope="col">Descripción</th>
                                    <th scope="col">Imagen</th>
                                    <th scope="col"></th>
                                </tr>
                            </thead>
                            <tbody>
            
                                {
                                    trabajos.map((e) =>
                                    (
                                       
                                        <Fila key={e.id} id={e._id} titulo={e.titulo} descripcion={e.descripcion} imagen={e.imagen} funcionEliminar={eliminar.bind(this, e._id)} fucnionEditar={editar.bind(this, e)}/>
            
                                    ))
            
                                }
            
                            </tbody>
                        </table>
                        <p>Trabajos registrados: {trabajos.length}</p>
            
            
            
            
            
                    </div>
            
            
            
            
                )

            }
               
    
    



  
}





function Fila({ id, titulo, descripcion, imagen, funcionEliminar, fucnionEditar}) {

      
    return (
        <tr>
            <th>{titulo}</th>
            <td>{descripcion}</td>
            <td><img src={imagen} alt={titulo} className='imagentabla' /></td>
            <td><button onClick={funcionEliminar} className="btn btn-danger">Eliminar</button> {/* <button onClick={fucnionEditar} className='btn btn-success'>Editar</button> */}</td>
            
        </tr>

    )
}

export default Trabajos
