import { useState } from 'react'
import { Link } from 'react-router-dom'
import '../Estilos/Login.css'
import { useNavigate } from 'react-router-dom'
import { useEffect } from 'react'


function Login() {
      
    

   
    const getData=(control)=>
    {   
        
         control.preventDefault();  
        Ejecutar(email, clave)
        

        
    }

    const newClave = (e)=>
    {
        let codigo =e.target.value;
        setClave(codigo)
    }

    const newEmail = (e)=>
    {
        let email =e.target.value;
        setEmail(email);
    }
    const [email, setEmail] = useState('')
    const [clave, setClave] = useState('')
    const [respuesta, setRespuesta] = useState({codStatus:'', mensaje: '', usuario: '', token: ''})
    const navegacion = useNavigate()
    
   

    const Ejecutar= async (email, clave)=>
    {
        const req ={"email" : email, "clave" : clave}
       const peticion= await   fetch('https://web-production-eeb4.up.railway.app/usuario/login',{
             method: 'POST',
             headers: {
                'Accept': 'application/json',
                'Content-Type':'application/json'
            }, 
            body: JSON.stringify(
                req
                )
        }).then(Response=>Response.json())

        const res = await peticion;
        setRespuesta(res)
        console.log(res)


        // ahora procedemos a redirigir si el estado es 200 
      
            if(res.codStatus==='200')
            {
                 console.log(res)
             
                localStorage.setItem('token', res.token)
                localStorage.setItem('user', JSON.stringify(res.usuario))
                localStorage.setItem('username', JSON.stringify(res.usuario.nombre) )
                localStorage.setItem('email', JSON.stringify(res.usuario.email))
                navegacion('/cm-diseno/admin/portadas') 
                window.location.reload();
                
            }
    
        
      
        // ahora guardamos el token en el local storage para gestiones de prueba
        
       
       
        

       
        
    }

  






    return (
        <div className='containerLogin'>
        <div className='Login'>
            <div className='panelLogin'></div>
            <form method="post"  className="formularioLogin" onSubmit={getData}>
                <h1 className="tituloLogin">Login</h1>
                 <div><label clssName='label'>USUARIO</label> <input className='form-control mb-3' type="text" required="true" placeholder="Usuario" onBlur={newEmail} id='textUsuario' /></div>  
                 <div><label className='label'>CONTRASEÑA</label><input  className='form-control mb-3'type="password" required='true' id='textClave' onBlur={newClave
                } placeholder="Contraseña"/> </div>
                
                 <input className='btn btn-dark' type="submit"  value='Iniciar Sesión' />
                 <span><p className='text-danger'>{respuesta.mensaje}</p></span>

                
            </form>
        </div>
        </div>
    )
}
export default Login