import '../Estilos/Mvv.css'
function Mvv({icono, titulo, descripcion})
{   
    return(

        <div className="tarjetaNosotros">
                <img src={icono} alt="" className="iconoMvv" />
                <h1 className="tituloMvv">{titulo}</h1>
                <p className="descripcionMvv">"{descripcion}"</p>
        </div>
    )

    
}
export default Mvv