
import '../Estilos/Nosotros.css'
import Aos  from 'aos'
Aos.init()

function Nosotros() {
    return (
        <div className="container-fluit" id='PanelNosotros'>
            <div className="text-center">
                <div className="row container" id="Nosotros">
                    <h1 className="tituloNosotros">¿Quienes Somos?</h1>
                    <hr />
                    <img src={require('../img/Nosotros.png')} alt="" className='imgNosotros' />
                    <p className='parrafoNosotros'>Somos un agencia de publicidad con el nombre de  <b>CM DISEÑO</b> en la cual nos dedicamos a desarrollar de manera autentica la creacion de la imagen y el estilo estetico de tu empresa de una manera creativa para iniciar tu identidad visual y publicitaria. </p>
                </div>
            </div>
        </div>
    )
}

export default Nosotros