import '../Estilos/PanelServicios.css'
import ImagenServicio from '../img/servicio.png'
import { useState } from 'react'




function TarjetaServicio({ titulo, descripcion, imgmagicard }) {

  
    // Esta logica funcionara para hacer funcionar la tarjeta magica de los card
    const [magic, setMagic] = useState('MagicCardFalse')
    const [imgMagic, setimgMagic] = useState('imgMagicFalse')
   
    // ahora creamos una funcion para cmabiar el estado de la imagen
    const MagicImg = () => {
        if (magic === "MagicCardFalse") {
            setMagic('MagicCardTrue')
        }
        else {
            setMagic('MagicCardFalse')
        }

        if (imgMagic === "imgMagicFalse") {
            setimgMagic('imgMagicTrue')
        }
        else {
            setimgMagic('imgMagicFalse')
        }



    }


    return (
        <div className='TarjetaServicio'>
           
            <img src={require(`../img/servicio.png`)} alt="" className='iconoServicio' />
           
           

            <p className='TituloServicio'><strong>{titulo}</strong></p>
            <hr />
            {console.log(titulo)}
            <p className='DescripcionServicio'>
                {descripcion}
            </p>

            <input className='btn btn-warning' type="button" value="Ver" onClick={MagicImg} />
            <div className={magic}>
                <img src={imgmagicard} alt="" className={imgMagic} />
            </div>



        </div>
    )
}

TarjetaServicio.defaultProps =
{
    titulo : "Sellos",
    descripcion: "Los sellos en las empresas no son obligatorios, pero si muy recomendables. Y es que se trata de elementos que pueden ser muy útiles en diferentes tareas. Los diferentes modelos de sellos pueden quitarte mucha más faena de lo que te imagina, sobre todo si tienes una empresa en la que el papeleo es constante.",
    imgmagicard : 'sellos.jpg'

}

export default TarjetaServicio