import { post } from 'jquery'
import { Link, useLocation } from 'react-router-dom'
import '../Estilos/Login.css'
import { useState } from 'react'
import Login from './Login'
import { Route } from 'react-router-dom'
import { useNavigate } from 'react-router-dom';
import 'sweetalert2/src/sweetalert2.scss'
import Swal from 'sweetalert2'








function RegistrarUsuario() {
    const [data, setData] = useState({ nombre: '', email: '', clave: "" })
    const [respuesta, setRespuesta] = useState()
    
    const navegacion = useNavigate();



    function Alert(titulo, icono) {
        Swal.fire({
            position: 'top-end',
            icon: icono,
            title: titulo,
            showConfirmButton: false,
            timer: 5000
        })
    }

    function MsjError() {

        Swal.fire({
            position: 'top-end',
            icon: 'success',
            title: 'Se ha insertado un registro correctamente!!',
            showConfirmButton: false,
            timer: 3000
        })
    }



    function Nombre(e) {

        setData((valores) =>
        ({
            ...valores,
            nombre: e.target.value,
        }))
    }

    function Email(e) {
        setData((valores) =>
        ({
            ...valores,
            email: e.target.value,
        }))
    }

    function Clave(e) {
        setData((valores) =>
        ({
            ...valores,
            clave: e.target.value,
        }))
    }





    const Procesar = (e) => {
         e.preventDefault(); 
       
        const dataJson = { "nombre": data.nombre, "email": data.email, "clave": data.clave }
        
        fetch('https://web-production-eeb4.up.railway.app/usuario', {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },  

            body: JSON.stringify(
                dataJson
            )
        }).then((Response) => Response.json()).then((datos) => {


            setRespuesta(datos);
         console.log(datos)
            if(datos[1]==='success')
            { 

               
                Alert(respuesta,  'success')
                setTimeout(() => {
                    navegacion('/cm-diseno/IniciarSesion')
                }, 6000)
            }
            else
            {
                
                console.log('test')
                Alert(respuesta,  'error')
                
            }

           

          


        })



        // ahora hacemos la peticion al servidor 
    }


 

    return (
        <div className='containerLogin'>
            <div className='Login'>
                <div className='panelLogin'></div>
                <form method="post" className="formularioLogin" onSubmit={Procesar}>
                    <h1 className="tituloLogin">Registrar Usuario</h1>
                    <div><label className='form-label'>NOMBRE</label> <input className='form-control mb-3' type="text" required="true" placeholder="Nombre" value={data.nombre} onChange={Nombre} /></div>
                    <div><label className='form-label'>EMAIL</label><input className='form-control mb-3' type="text" required='true' placeholder="Email" value={data.email} onChange={Email} /> </div>

                    <div><label className='form-label'>CLAVE</label><input className='form-control mb-3' type="password" required='true' placeholder="Clave" value={data.clave} onChange={Clave} /> </div>
                    <input className='btn btn-dark' type="submit" value='Registrar' />
                    <br />

                    
                   
                    

                    
                
                </form>




            </div>
        </div>
    )
}
export default RegistrarUsuario

