import '../Estilos/PanelPortafolio.css'
import Trabajos2 from './Trabajos2'


function PanelPortafolio() {
    return (
        <div className='container-fluit text-center'>
            <h1 className='tituloNosotros'>"Trabajos Realizados"</h1>
            <p className='subTituloNosotros'> *** EN ESTA SECCION PODRAS OBSERVAR ALGUNOS DE NUESTROS TRABAJOS REALIZADOS A LO LARGO DEL TIEMPO ***</p>
            <div className="PanelPortafolio" id='portafolio'>

                <Trabajos2 />
            </div>
        </div>



    )
}


function Trabajos({ imgTrabajo }) {
    return (
        <div className='cardJob'>
            <img src={require(`../img/${imgTrabajo}`)} alt="" />


        </div>
    )
}

export default PanelPortafolio