import { SwitchTransition } from 'react-transition-group'
import '../Estilos/PanelInfo.css'
import { Link } from 'react-router-dom'
import { useState } from 'react'
import { useEffect } from 'react'
import iconoUusario from '../img/user.png'
import { error, timers } from 'jquery'
import { useNavigate } from 'react-router-dom'


function PanelInfo({ telefono, email, usuario }) {
    const navegacion = useNavigate()
    const [date, setDate] = useState();
    const [altura, setAltura] = useState("0px")
    const [username, setUsername] = useState(localStorage.getItem('username'))

    const [interruptor, setInterruptor] = useState(0);

    window.addEventListener('load', () => {
        const array = new Array();
        let name = localStorage.getItem('user');

        array.push(name)


    })




    setTimeout(() => {
        var fecha = new Date();
        var now = fecha.toLocaleString();
        setDate(now)
    }, 1000)

    const menuUserStyle =
    {
        height: altura

    }

    // funcion para cambiar la altura del boton de del menu de user

    const accionMenu = () => {

        if (interruptor === 0) {
            setInterruptor(1)
            setAltura("100px")
            console.log('interruptor true')
            console.log(interruptor)

        }
        else {
            setAltura("0px")
            setInterruptor(0)
            console.log('interruptor else')
            console.log(interruptor)

        }



        //
        if (interruptor === 0) {
            setTimeout(() => {
                console.log('testiando')
                setAltura("0px")
                setInterruptor(0)

            }, 10000);
        }

    }


    const cerrarSesion = async ()=>
    {

        const ruta = 'https://web-production-eeb4.up.railway.app/usuario/logout';
        const token = localStorage.getItem('token');
        const confirmacion =  window.confirm('Esta seguro que desea cerrar la sesión?');
        
         if(token && confirmacion)
        {
        
            const peticion= await fetch(ruta, { method: 'POST',  headers: {Authorization : token}})
            const respuesta = await peticion;
            if(respuesta.status===200)
            {
                localStorage.clear();
                navegacion('/')
                window.location.reload();
                
               
            }

            
        } 
    }



    // de aqui en adelante es el render

    if(username!==null)
    {
    
        return (

            <div className="contenedor-principal-panel-info container-fluit" id='Inicio'>
                <div className="container contenedor-info">
                    <div className="panel-info-contactos">
                        <div className="panel-info-telefono"> <i class="bi bi-telephone-outbound-fill"> {telefono}</i>    </div>
                        <div className="panel-info-email"> <i class="bi bi-envelope-at-fill"> {email}</i> </div>
                        <div className="panel-info-email"> <i class="bi bi-calendar-check-fill"> {date}</i> </div>
    
                    </div>
    
                    <div className="panel-info-redes">
    
                        <a href='https://www.instagram.com/p/CQWf1ittQ6m/?utm_medium=copy_link' target={'_blank'}><i class="bi bi-instagram"></i></a>
    
                        <a href=''><i class="bi bi-facebook"></i></a>
                        <a href='https://wa.me/8297192352' target='_blank'> <i class="bi bi-whatsapp"></i></a>
    
                        <div className='menuUsuario'>
                            <img src={iconoUusario} alt="" id='iconoUsuario' onClick={accionMenu} />
                            <div id='contenidoUsuario' style={menuUserStyle}>
                                <div className='text-light'>{username}</div>
                                <div><Link className='LinkUser' to='/'>HOME</Link></div> 
                                <div><Link className='LinkUser' to='/cm-diseno/admin/portadas'>ADMIN</Link></div> 
                                 <div><Link className='LinkUser'  onClick={cerrarSesion}>CERRAR SESIóN</Link></div> 

                               {/*      <div><button className='LinkUser btn' onClick={cerrarSesion}>CERRAR SESIóN</button></div> */}
                               
    
                            </div>
    
                        </div>
    
                    </div>
                </div>
    
            </div>

        )

    }
    else
    {
       
        
    return (

        <div className="contenedor-principal-panel-info container-fluit" id='Inicio'>
            <div className="container contenedor-info">
                <div className="panel-info-contactos">
                    <div className="panel-info-telefono"> <i class="bi bi-telephone-outbound-fill"> {telefono}</i>    </div>
                    <div className="panel-info-email"> <i class="bi bi-envelope-at-fill"> {email}</i> </div>
                    <div className="panel-info-email"> <i class="bi bi-calendar-check-fill"> {date}</i> </div>

                </div>

                <div className="panel-info-redes">

                    <a href='https://www.instagram.com/p/CQWf1ittQ6m/?utm_medium=copy_link' target={'_blank'}><i class="bi bi-instagram"></i></a>

                    <a href=''><i class="bi bi-facebook"></i></a>
                    <a href='https://wa.me/8297192352' target='_blank'> <i class="bi bi-whatsapp"></i></a>

                    <div className='menuUsuario'>
                        <img src={iconoUusario} alt="" id='iconoUsuario' onClick={accionMenu} />
                        <div id='contenidoUsuario' style={menuUserStyle}>
                            <div className='text-light'>{username}</div>

                            <div><Link className='LinkUser' to='/cm-diseno/IniciarSesion'>LOGIN</Link></div>
                            <div><Link className='LinkUser' to='/cm-diseno/registrarUsuario'>REGISTRARSE</Link></div>

                        </div>

                    </div>

                </div>
            </div>

        </div>

    )

    }


}

PanelInfo.defaultProps =
{
    telefono: '829-719-2352',
    email: 'cmdiseño6@gmail.com'
}

export default PanelInfo
