import '../Estilos/PanelPortadas.css'
import { useState } from 'react'






function PanelPortadas() {

    
    const [portada, setPortada] = useState('')
    const [posicion, setPosicion] = useState(0);
    const [longitudData, setLongitudData] = useState(0)
    
    // funcion para cambiar una imagen hacia delante

    const getData =  (ubicacion)=>
    {
       
        fetch('https://web-production-eeb4.up.railway.app/portadas').then(Response=>Response.json())
        .then(datos=>
            {
                const url = datos[ubicacion]['url']
                setPortada(url)
                const l =  datos.length;
                setLongitudData(l)  
                    
            })
            
    }

        getData(posicion)
       


    const adelantarPortada = () => {
        
           let valor = posicion + 1;
           if(valor<longitudData)
           { 
                setPosicion(valor)
                console.log(posicion);
           }
           else
           {
                valor=0;
                setPosicion(valor)
                console.log("falso" + posicion);
           }
           
        
    }
// funcion cambiar una imagen hacia atras
    const retrocederPortada = () => {
        let valor = posicion - 1;
        if(valor<0)
        { 
             setPosicion(0)
             console.log("menor que cero" + posicion);
            
        }
        else
        {
            
             setPosicion(valor)
             console.log("falso" + posicion);
        }
        
        
    
    }

      
     
    return (
        <div style={{ backgroundImage : `url(${portada})`}} className='PanelPortada'>
                     
           <div className='botonescontenedor'>
                <button className='btnAccion' onClick={retrocederPortada}>{'<'}</button>
                <button className='btnAccion' onClick={adelantarPortada}>{'>'}</button>
            </div>



        </div>
    )
}

PanelPortadas.defaultProps =
{
    portada: '1'
}
export default PanelPortadas